*,
*::before,
*::after {
	box-sizing: border-box;
	font-family: inherit;
}

body {
	margin: 0;
	min-width: 320px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-decoration-skip-ink: none;
	font-family: "ZeroCool", "PT Sans", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
		"Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
		sans-serif;

	/* background: radial-gradient(circle at 100% 50%, #6868ff, transparent 60%), linear-gradient(45deg, #7e6ed3, #95cdff); */
	--_cellSize: 25px;
	--_strokeWidth: 2px;
	--_strokeColor: #ffffff11;
	background: repeating-linear-gradient(90deg, var(--_strokeColor) 0px, var(--_strokeColor) var(--_strokeWidth), transparent var(--_strokeWidth), transparent var(--_cellSize)),
		repeating-linear-gradient(0deg, var(--_strokeColor) 0px, var(--_strokeColor) var(--_strokeWidth), transparent var(--_strokeWidth), transparent var(--_cellSize)),
		linear-gradient(0deg, #3035dc, #120cbc);
	color: white;
}
body:has(.calmBack) {
	background: linear-gradient(0deg, #3035dc, #120cbc);
}
.calmBack {
	position: fixed;
}

pre,
code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
		monospace;
}

.root {
	min-height: 100vh;
	min-height: 100dvh;
	display: flex;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	font-weight: normal;
}
h1 {
	text-align: center;
}

button {
	cursor: pointer;
}

::-webkit-scrollbar {
	width: 4px;
	height: 4px;
}

::-webkit-scrollbar-thumb {
	background-color: gray;
}

.title {
	color: #ffc95e;
	text-shadow: 0.1em 0 #a42142, -0.1em 0 #a42142, 0 0.1em #a42142, 0 -0.1em #a42142,
		0.05em 0.05em #a42142, -0.05em -0.05em #a42142, 0.05em -0.05em #a42142, -0.05em 0.05em #a42142;
}
